  #TalClassAttendance {
    overflow-x: hidden;
    padding: 10px;
  }
  .class_top {
    background: #fff;
    padding-top: 24px;
    padding-bottom: 10px;
    font-size: 14px;
  }
  .info {
    display: flex;
    margin-bottom: 10px;
    span {
      margin-right: 50px;
    }
  }
  .is-change {
    background: #fde5cd;
  }

</style>
